.chip-container {
    list-style-type: none;
    padding-left: 0px;
    display: flex;
    column-gap: 0.625rem;
    row-gap: 0.625rem;
    flex-wrap: wrap;
    /* width: calc(50% - 1px); */
}

.chip-label-container {
    /* flex: .1; */
}

.chip {
    height: 28px;
    display: flex;
    align-items: center;
    /* column-gap: 5px; */
    cursor: pointer;
    border: 0.0625rem solid rgba(49, 51, 63, 0.2);
    border-radius: 0.25rem;
    background-color: transparent;
    padding-left: 0.46875rem;
    padding-right: 0.46875rem;
}

.chip.active {
    color: rgb(49, 51, 63);
    border-color: rgb(0, 0, 0);
}

.chip-label-container#not-available {
    pointer-events: none;
    opacity: .6;
}

.chip-status {
    width: .9rem;
    margin-right: 1.5px;
}

#status-icon {
    font-size: 13px;
}

.chip-label {
    padding-right: 10px;
    width: 100%;
    white-space: nowrap;
    text-align: center;
    font-size: 0.75rem;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
}